import "../css/Testimonials2.css";
import "../css/RootStyles.css";

import iconRate from "../../Icons/rating4.png"
import iconAvatar1 from "../../Icons/testimonials-avatars/default-avatar-2020-1.jpg";
import iconAvatar2 from "../../Icons/testimonials-avatars/default-avatar-2020-65.jpg";
import iconAvatar3 from "../../Icons/testimonials-avatars/default-avatar-2020-42.jpg";
import { Carousel } from "react-responsive-carousel";


function Testimonials2() {
    return (
        <div className="Testimonials2-main-container ">
            
            <figure class="shadow-1 snip1157 border-rounded-g10 flex-column  ">

                <div className="snip-filter"></div>
                <blockquote className="text-center">Pasamos un tiempo increble en las montaas con nuestro gua Janier de Nevado Trek! Nos llevaron por los senderos ms bellos del parque nacional y hasta la cumbre del Nevado Tolima. La subida al Nevado Tolima es realmente extenuante y difcil, pero gracias a nuestro gua logramos llegar a la cima y experimentar la fascinante salida del sol all arriba.
                                Realmente recomendamos volver a reservar un recorrido con Nevado Trek!.
                    <div class="arrow"></div>

                </blockquote>

                <div className="flex-row">
                    <img src={iconAvatar1} alt="sq-sample3"/>
                    <div class="author">

                        <h5>@ElisabethvanBerckel

                        </h5>
                        
                        <img alt="testimonial" className="Figure-rating-img"
                            src={iconRate}/>

                    </div>

                </div>

            </figure>
            <figure class="snip1157 border-rounded-g10 flex-column ">

                <div className="snip-filter"></div>
                <blockquote className="text-center">Pas un tiempo maravilloso haciendo la caminata de 4 das hasta la cumbre del Nevado del Tolima. Todas las granjas en las que me aloj eran hermosas y la gente amable. El clima era ideal el da de la cumbre,  tuve una excelente gua, Janier, y llegamos a la cima.
                    <div class="arrow"></div>

                </blockquote>

                <div className="flex-row">
                    <img src={iconAvatar2} alt="sq-sample3"/>
                    <div class="author">

                        <h5>@ashack3187

                        </h5>
                        <img alt="testimonial" className="Figure-rating-img"
                            src={iconRate}/>

                    </div>

                </div>

            </figure>
            <figure class="snip1157 border-rounded-g10 flex-column ">

                <div className="snip-filter"></div>
                <blockquote className="text-center">Qu increble aventura. Janier y todos los que ayudaron a organizar esto fueron increbles. Nunca olvidaremos este viaje. Janier fue paciente y amable conmigo cuando estaba aterrorizada de llegar a la cima. Y me ayud a bajar porque estaba tan asustada jajaja. Absolutamente increble que recomiendo 100%!
                    <div class="arrow"></div>

                </blockquote>

                <div className="flex-row">
                    <img src={iconAvatar3} alt="sq-sample3"/>
                    <div class="author">

                        <h5>@KarisaFenty

                        </h5>
                        <img alt="testimonial" className="Figure-rating-img"
                            src={iconRate}/>

                    </div>

                </div>

            </figure>
        </div>


    )
}


export {
    Testimonials2
}
